
body{
	background-color: rgb(174, 125, 125);
	color: rgb(255, 255, 255);
	font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

p{
	font-weight: 600;
	font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
	color: black;
}

.amount{
	font-size: 1.5em;
	font-weight: 500;
	color: black;
}

.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dcfb;
}

.basketItem, .basketItem_price{
	background-color: rgb(119, 33, 112);
	color: white;
	font-weight: 600;
	font-size: 1.2em;
	box-sizing: border-box;
	text-transform: capitalize;
	padding: 1em;
	margin: 1em;
	text-align: start;
	border-radius: 0.4em;
}

.basketItem_price{
	background-color: rgb(141, 85, 113);
	margin: 4em 1em 1em 1em;
	padding: 0.5em;
	border-radius: 0.2em;
}

button{
	border-radius: 0.5em;
	background-color: azure;
	border: 3px solid rgb(247, 225, 228);
	font-size: 1.1em;
}

.button_add{
	margin: 0 1.2em .5em 1.2em;
	width: 50px;
	height: 50px;
	font-size: 1.1em;
	background-color: azure;
}


.button_cart, .button_cartbuy{
	margin: 0.3em 0em 0.3em 0em;
	color: rgb(75, 73, 73)
}

.button_cartbuy{
	font-size: 2em;
	padding: 0.2em 0.5em;
	color: black;
}

.button_cart{
	margin: 0.6em;
}

.button_front{
	border: none;
	background-color: inherit;
	font-weight: 600;
	font-size: 1.2em;
}

.cart_container{
	position: fixed;
	margin-top: 15em;
}

.cart{
	background-color: rgb(117, 230, 230);
	color: darkgoldenrod;
	border-radius: 2em;
	border: 0.3em solid brown;
	min-height: 30em;
	width: 15em;
	margin-right: 1em;
	overflow-y: hidden;
}

.checkout_button{
	border: none;
	width: 12em;
	background-color: rgb(201, 190, 34);
	font-size: 1.1em;
	font-weight: 600;
}

.errormsg{
	padding: 1em;
	color: red;
	background-color: rgb(119, 33, 112);
	font-size: 2em;
}

.flex_column
{
	display: flex;
	flex-direction: column;
}

.flex_horizontal, .product_container, .flex_rightside
{
	display: flex;
	justify-content: center;
}

.flex_rightside
{
	justify-content: right;
}

form
{
	margin-top: 4em;
}

h1, h2
{
	text-transform: capitalize;
	font-size: 2.5em;
	margin-top: 8em;
	letter-spacing: 0.07em;
	text-decoration: underline;
}

h2
{
	margin-top: 4em;
}

h3
{
	margin: 1em;
	font-size: 4em;
	margin-top: 1em;
}


label
{
	text-transform: capitalize;
	font-size: 1.6em;
	font-weight: 600;
	margin: 1em 2em;
}

.product_container
{
	flex-wrap: wrap;
}

.img_product{
	height: 10em;
}

input{
	font-size: 1.2em;
	width: 20em;
	padding: 0.7em;
	align-self: flex-end;
	margin: 1em 1em;
	display: inline-block;
	border-radius: 1em;
}

.left{
	text-align: left;
	font-size: 2em;
}

.nnl{
	display: inline;
}

.old_price{
	text-decoration: line-through;
}

.product_card{
	background-color: bisque;
	border: 3px solid rgb(224, 171, 105);
	border-radius: 10%;
	box-sizing: border-box;
	padding: 1em;
	margin: 2em 1em;
	width: 30em;
}

.product_header{
	font-size: 2em;
	font-weight: 600;
	font-style: oblique;
}

.sidebar{
	width: 20%;
	min-width: 16em;
}

table
{
	background-color: white;
	border: 1px solid black;
	border-spacing: 0;
	color: black;
}


td
{
	border: 1px solid black;
	padding: 0px 60px;
}

.tfirstrow{
	font-weight: 700;
}

.tmid{
	text-align: center;
}

.trigh{
	text-align: right;
}


@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
